export default {
	data() {
		return {
			yonghuwx: [],
			phone: 123456789,
			motto: '解绑手机号'
		}
	},
	onLoad() {
		let that = this;
		uni.login({
			provider: 'weixin',
			success: function(loginRes) {
				// 获取用户信息				
				uni.getUserInfo({
					provider: 'weixin',
					success: function(infoRes) {
						that.yonghuwx = infoRes.userInfo
					}
				});
			}
		});
	}
}
